#sprites-list-component {
  grid-column: 5;
  grid-row: 1;
  background-color: #f7f7f7;
  border-radius: 5px;
  border: 1px solid #ccc;
  box-sizing: border-box;
  margin: 5px;
  padding: 5px;
  max-height: 100vh;
  overflow-y: scroll; }
  #sprites-list-component > div {
    margin: 5px 0; }
    #sprites-list-component > div:first-of-type {
      margin: 0; }
  #sprites-list-component .github-buttons {
    text-align: right;
    display: flex;
    justify-content: center; }
  #sprites-list-component .drop-zone, #sprites-list-component .drop-zone-empty, #sprites-list-component .drop-zone-completed {
    display: flex;
    justify-content: center;
    flex-direction: column;
    text-align: center;
    width: 100%;
    box-sizing: border-box;
    border: 2px dashed #22bed3; }
    #sprites-list-component .drop-zone-empty {
      height: 95%; }
      #sprites-list-component .drop-zone-empty .drop-zone-text:after {
        content: 'Drop or click for select JSON here.'; }
    #sprites-list-component .drop-zone-completed .drop-zone-text:after {
      content: 'Update JSON file.'; }
    #sprites-list-component .drop-zone .drop-zone-text, #sprites-list-component .drop-zone-empty .drop-zone-text, #sprites-list-component .drop-zone-completed .drop-zone-text {
      color: #1b97a7;
      font-weight: 600;
      padding: 5px; }
  #sprites-list-component .sprite-item {
    background: #e0f7fa;
    padding: 5px;
    border-radius: 10px 0;
    border: 1.5px solid #72dbe8;
    color: #1b97a7;
    font-weight: 600;
    font-family: system-ui, serif;
    cursor: pointer; }
    #sprites-list-component .sprite-item:hover, #sprites-list-component .sprite-item.selected {
      background-color: #a7e8f1;
      color: #146f7b; }
  #sprites-list-component .additional-info {
    animation: openAdditionalInfo 0.5s ease;
    background: #fff;
    padding: 5px;
    margin-bottom: 5px;
    border-radius: 0 0 5px 5px;
    color: #555;
    overflow-y: hidden;
    font-size: 0.9em; }
    #sprites-list-component .additional-info .name {
      font-weight: 700; }

@keyframes openAdditionalInfo {
  0% {
    opacity: 0.6;
    max-height: 0; }
  100% {
    opacity: 1;
    max-height: 300px; } }



